.jumbotron {
    margin-bottom: 15px;
    width: 100%
}

.header {
    background-color: #2c323c;
    color: #fff;
    min-height: 200px;
    text-align: center
}

.header h1 {
    margin: 0;
    margin-bottom: 30px;
    font-size: 4rem
}

.container {
    display: -ms-flexbox;
    display: flex;
    min-height: 80%;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-pack: center;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto
}


